.sw-update {
  text-align: center;
  color: white;
  position: fixed;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;

  .sw-update-overlay {
    height: 100%;
    width: 100%;
    opacity: 0.8;
    background-color: black;
    position: fixed;
    z-index: 11;
  }

  .sw-update-content {
    font-size: 3em;
    font-family: fantasy;
    position: relative;
    z-index: 12;
    top: calc(50vh - 3em);
  }
}