.login {
  position: fixed;
  z-index: 100;
  font-family: chalk-board, sans-serif;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 0.2em;
  width: 100%;
  height: 100%;
  vertical-align: center;
  text-align: center;

  &__welcome-left, &__welcome-right {
    font-size: 4em;
    box-sizing: border-box;
    border: 0.25em solid #d2cfcf;
    display: flex;
    flex-direction: column;
    background-color: #014c01;
    color: white;

    div {
      line-height: 4em;
      text-align: left;

      &.small {
        font-size: 0.5em;
      }
    }

    form {
      input {
        vertical-align: middle;
        padding: 0.5em 0.5em;
        width: 8em;
        margin: 0 0.25em;
        border: none;
        outline: 0;

        &.error {
          animation: shake 0.2s ease-in-out 0s 2;
          box-shadow: 0 0 1em red;
        }
      }
      button {
        vertical-align: middle;
        border: none;
        outline: 0;
        background-color: transparent;
        color: white;

        svg.icon {
          vertical-align: middle;
        }
      }
    }
  }
  &__welcome-left div {
    text-align: right;
  }
  &--open {
    .login__welcome-left {
      transform: perspective(500vw) rotateY(-120deg);
      transform-origin: 0 0;
      animation: left-open 3s ease-in 0s 1;
    }
    .login__welcome-right {
      transform: perspective(500vw) rotateY(120deg);
      transform-origin: 50vw 0;
      animation: right-open 3s ease-in 0s 1;
    }
  }
}

@keyframes left-open {
  from {
    transform: perspective(500vw) rotateY(0deg);
    transform-origin: 0 0;
  }
  to {
    transform: perspective(500vw) rotateY(-120deg);
    transform-origin: 0 0;
  }
}

@keyframes right-open {
  from {
    transform: perspective(500vw) rotateY(0deg);
    transform-origin: 50vw 0;
  }
  to {
    transform: perspective(500vw) rotateY(120deg);
    transform-origin: 50vw 0;
  }
}

@keyframes shake {
  0% { margin-left: 0; }
  25% { margin-left: 0.5rem; }
  75% { margin-left: -0.5rem; }
  100% { margin-left: 0; }
}