.App {
  .App-content {
    min-height: calc(100vh - 2.2em);
    padding: 0.1em 1em;
  }

  .App-logo {
    height: 1.5em;
    padding: 0.25em;
  }

  .App-header {
    display: flex;
    justify-content: space-between;
    background-color: beige;
    height: 2em;
    color: white;

    .navigation {
      display: flex;
      align-items: center;
      justify-content: center;

      button {
        border: none;
        padding: 0.25rem;
        margin-right: 0.25rem;
        border-radius: 0.25rem;
        background-color: black;
        color: white;
        outline: none;
      }
    }
  }
}
