table {
  width: 100%;
  border-collapse: collapse;
  margin:50px auto;
  color: black;

  /* Zebra striping */
  tr:nth-of-type(even) {
    background: #eee;
  }

  thead {
    box-shadow: 0 4px 6px -4px black;
    th {
      background: beige;
      font-weight: bold;
    }
  }

  td, th {
    padding: 10px;
    border: 1px solid #ccc;
    text-align: center;
  }

  td:first-child {
    text-align: left;
  }

  tr.evil {
    td:first-child {
      background: red;
      color: white;
    }
  }
}
