.calendar {
  text-align: center;

  .month {
    font-size: 1.5em;
    margin-bottom: 1em;

    .value {
      width: 10em;
    }

    .arrow {
      cursor: pointer;
      padding: 0.5em;
      vertical-align: middle;
      .icon {
        height: 1em;
      }
    }
  }

  .grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;

    .weekday {
      padding-bottom: 0.5em;
      border-bottom: 1px solid black;
    }

    .date {
      border-right: 1px solid black;
      border-bottom: 1px solid black;

      &.monday {
        border-left: 1px solid black;
      }

      text-align: left;
      height: 5em;
      position: relative;

      &.inactive {
        background-color: #ccc;
      }

      &.current {
        background: radial-gradient(circle at 0% top, #075305, transparent 75%);

        .value {
          color: white;
          border-radius: 0 0 2em 0;
        }
      }

      .value {
        font-weight: bold;
        padding: 0.1em 1em 1em 0.25em;
      }

      .event {
        cursor: pointer;
        position: absolute;
        overflow: hidden;
        white-space: nowrap;
        background-color: #636358;
        color: white;
        padding: 0 0.25em;

        &.event-0 {
          top: 1em;
        }
        &.event-1 {
          top: 2.2em;
        }
        &.event-2 {
          top: 3.4em;
        }

        &.single {
          margin: 0 0.25em;
          border-radius: 0.25em;
        }
        &.begin {
          margin-left: 0.25em;
          border-radius: 0.25em 0 0 0.25em;
        }
        &.end {
          margin-right: 0.25em;
          border-radius: 0 0.25em 0.25em 0;
        }
      }
    }
  }
}

.selected-event {
  position: absolute;
  width: 100vw;
  z-index: 2;
  top: 0;
  left: 0;
  background-color: beige;
  animation: fadeIn 0.5s ease-in;
  box-shadow: 0 0 1rem 0.5rem black;

  .close-btn {
    cursor: pointer;
    position: absolute;
    right: 1rem;
    top: 0.75rem;
  }

  .icon {
    height: 1.4em;
    vertical-align: bottom;
    width: unset;
    margin-right: 0.25em;
  }

  .selected-event-container {
    .description {
      margin: 1rem;

      .title {
        font-weight: bold;
        font-size: 1.2em;
      }

      .date-label {
        display: inline-block;
        width: 5em;
      }
    }
  }
}

@keyframes slideIn {
  0% { top: -100vh }
  100% { top: 0 }
}
@keyframes fadeIn {
  0% { opacity: 0 }
  100% { opacity: 1 }
}
