@import "variables";
@import "App";
@import "duties";
@import "review";
@import "calendar";
@import "detentions";
@import "login";
@import "tasks";
@import "sw";

svg.icon {
  width: 1em;
}

svg.reflect {
  -moz-transform: scaleX(-1);    /* Gecko */
  -o-transform: scaleX(-1);      /* Opera */
  -webkit-transform: scaleX(-1); /* Webkit */
  transform: scaleX(-1);         /* Standard */
}

html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

hr {
  display: block;
  overflow: hidden;
  border-style: inset;
  border-width: 1px;
}
