.detentions {
  .detention {
    margin: 1em 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1em;

    &__progress {
      grid-column: 2 / -1;
      position: relative;
      width: 50vw;
      background-color: darkgrey;
      height: 1em;
      border-radius: 0.5em;

      &--value {
        border-radius: 0.5em 0 0 0.5em;
        height: 100%;
      }
    }

    &__statistic {
      grid-column: 2 / -1;

      .bad {
        color: red;
      }
    }
  }
}